import React from 'react';
import styled from 'styled-components';
import posed from 'react-pose';

const Box = styled(posed.div({

  start: {x: "-100%", opacity: 0},
  end: {x: "0%", opacity: 1 },  transition:{
      duration: 210,
      ease: 'linear'
    }
}))`
font-size: 1em;
font-family: 'Montserrat', sans-serif;
&:before {
 content: '';
 width: ${props=> props.width};
 height: 100%;
 position: absolute;
 background: #30ABE8;
 transition: 0.37s;
  transition-timing-function: cubic-bezier(2.0, 0.5, 0.1, 0.5);
}

`;

class Home extends React.Component  {
  constructor(props){
    super(props);
    this.state = {stage : "end", width: "100%"};
  }

 componentDidMount(){this.setState({stage: "end"});

 setTimeout(()=>this.setState({ width: "0%"}), 50);

}
  render(){
    return (<div>
    <div className="home grid align-center" >
          <div className="content-container align-left" >
          <Box pose={this.state.stage} width = {this.state.width}>
            Hello, I am <span >Aayush!</span> I am a junior at the ULM pursuing a Computer Science degree.
            I am a web developer, focused on <span >MERN</span> stack development. I love building responsive, progressive web apps.<br/>
            Check out some of my projects, my resume or send me an email.
          </Box>
          </div>
    </div>
    </div>
    );
  }
}
export default Home;
